.scrolled {
  background-color: #44b3c9;
  -webkit-box-shadow: 0 8px 6px -6px #507880;
  -moz-box-shadow: 0 8px 6px -6px #507880;
  box-shadow: 0 8px 6px -6px #507880;
}

@media (max-width: 768px) {
  nav.navbar {
      transition:  0.32s ease-in-out;
      background-color: #5ebdd0;
      -webkit-box-shadow: 0 8px 6px -6px #507880;
      -moz-box-shadow: 0 8px 6px -6px #507880;
      box-shadow: 0 8px 6px -6px #507880;
    }
}
